import React from "react";
import "./styles.css";
import { useTranslation } from "react-i18next";
import randapp from './RandomAppScreen.png';

export default function Projects() {
  const [t, i18n] = useTranslation("global");

  return (
    <div>
    <div className="project-container">
      <div className="project">
        <h4>e-Commerce</h4>
        <img src="https://camo.githubusercontent.com/d2d1a143beb752aad454a314b813dc315f8630fa259a867834351d3cbf3c7aa6/687474703a2f2f7265732e636c6f7564696e6172792e636f6d2f676f6e7a616c6f616775696c61726d2f696d6167652f75706c6f61642f76313631323139353933322f50726f64756374735f6672396f36622e6a7067" className="ecom"/>
        <p>
          {t("ecom.description")}
          <br />
          {t("ecom.technologies")} React | Redux | JavaScript | Sequelize | HTML5 | CSS3 | Styled Components | React Hook Forms | Reactstrap | Express | Passport | Bcrypt
        </p>
        {/* <button className="btn">{t("projects.boton3")}</button>
        */}
        <a href="https://github.com/artoria-95/ecommerce" target="_blank">
          <button className="pbtn">{t("projects.boton2")}</button>
        </a>
      </div>
      <div className="project">
        <h4>HENRY App</h4>
        <img src="data:image/png;base64," className="happ"/>
        <p>
          {t("HENRYApp.description")}
          <br />
          {t("HENRYApp.technologies")} React | Redux | JavaScript | MongoDB | HTML5 | CSS3 | Styled Components | React Hook Forms | Yup | Express
        </p>
        {/* <button className="btn">{t("projects.boton3")}</button>
         */}
        <a href="https://github.com/AlejoGschwind/HenryApp" target="_blank"><button className="pbtn">{t("projects.boton2")}</button></a>
      </div>
      <div className="project">
        <h4>The Random App</h4>
        <img src={randapp} className="ranapp"/>
        <p>
          {t("randomapp.description")}
          <br />
          {t("randomapp.technologies")} React | JavaScript | HTML5 | CSS3 
        </p>
        {/* <button className="btn">{t("projects.boton3")}</button>
        */}
        <a href="https://artoria-95.github.io/The-Random-App/" target="_blank">
          <button className="pbtn">{t("projects.boton1")}</button>
        </a>
        <a href="https://github.com/artoria-95/The-Random-App" target="_blank">
          <button className="pbtn">{t("projects.boton2")}</button>
        </a>
      </div>
    </div>
    <a href="#contact">
    <button className="pbtn">{t("projects.boton4")}</button>
    </a>
    </div>
  );
}
